:root {

    /* 'solid colors' */

    --azul: #3772FF;
    --white: hsl(0, 0%, 100%);
    --light-grayish-violet: hsl(270, 3%, 87%);
    --dark-grayish-violet: hsl(279, 6%, 55%);
    --very-dark-violet: hsl(278, 68%, 11%);

    /* 'gradient colors' */

    /* (active input border) */
    --linear-gradient: linear-gradient(to right, hsl(249, 99%, 64%), hsl(278, 94%, 30%));
      /* (input errors) */
    --red: hsl(0, 100%, 66%);

}
